import React, { useEffect, useRef, useState } from 'react'
import { FaceTracker } from '../FaceTracker'
import { ARSceneAFrame } from '../ARSceneAFrame'
import { useRA } from '../../contexts/RAContext'
import type { ScreenType, TransitionType, TransitionDirection } from '../../types/screens'
import '../../styles/ar-screen.css'

interface ARScreenProps {
  onNavigate: (screen: ScreenType, transition?: TransitionType, direction?: TransitionDirection) => void
  title?: string
  subtitle?: string
  backgroundImage?: string
}

export const ARScreen: React.FC<ARScreenProps> = () => {
  const { raData } = useRA()
  const config = raData?.configuracoes || {}
  const usarAFrame = config.usarAFrame !== false
  const usarVideo = config.usarVideo !== false
  const usarFaceTracking = config.usarFaceTracking !== false

  const [arLoading, setArLoading] = useState(true)
  const videoRef = useRef<HTMLVideoElement | null>(null)
  const mediaStreamRef = useRef<MediaStream | null>(null)

  // Mostrar vídeo - usar o vídeo global (se habilitado)
  useEffect(() => {
    if (!usarVideo) {
      setArLoading(false)
      return
    }

    async function setupCamera() {
      try {
        // Usar o vídeo global que já foi criado
        const video = document.getElementById('arjs-video') as HTMLVideoElement
        if (!video || !video.srcObject) {
          console.warn('Vídeo global não encontrado, aguardando...')
          await new Promise(resolve => setTimeout(resolve, 1000))
          const retryVideo = document.getElementById('arjs-video') as HTMLVideoElement
          if (!retryVideo || !retryVideo.srcObject) {
            console.error('Vídeo ainda não disponível')
            setArLoading(false)
            return
          }
        }

        const videoElement = video || (document.getElementById('arjs-video') as HTMLVideoElement)
        if (videoElement && videoElement.srcObject) {
          // Vídeo já existe, apenas mostrar
          videoElement.classList.add('ar-video-visible')
          videoRef.current = videoElement
          mediaStreamRef.current = videoElement.srcObject as MediaStream

          setArLoading(false)
        } else {
          console.error('Vídeo não tem stream')
          setArLoading(false)
        }
      } catch (err) {
        console.error('Erro ao configurar câmera:', err)
        setArLoading(false)
      }
    }

    setupCamera()

    return () => {
      // Não limpar aqui - deixar o vídeo para o FaceTracker
    }
  }, [usarVideo])

  // Adicionar elementos do A-Frame Scene e Image Tracking
  useEffect(() => {
    if (!usarAFrame) return

    const sceneEl = document.querySelector('a-scene#ar-scene-main') as any
    if (!sceneEl) {
      console.warn('A-Frame Scene não encontrado')
      return
    }

    // Configurar AR.js no scene se ainda não estiver configurado
    if (!sceneEl.hasAttribute('arjs')) {
      sceneEl.setAttribute('arjs', 'trackingMethod: best; sourceType: webcam; debugUIEnabled: false; detectionMode: mono_and_matrix; matrixCodeType: 3x3; cameraParametersUrl: https://cdn.jsdelivr.net/gh/AR-js-org/AR.js@master/data/data/camera_para.dat; maxDetectionRate: 60; canvasWidth: 640; canvasHeight: 480;')
      console.log('✅ AR.js configurado no scene')
    }

    // Adicionar câmera (AR.js gerencia a câmera, mas precisamos de uma entidade camera para o A-Frame)
    let camera = document.getElementById('camera')
    if (!camera) {
      camera = document.createElement('a-entity')
      camera.id = 'camera'
      // Para AR.js, a câmera precisa estar configurada de forma específica
      camera.setAttribute('camera', 'active: true; fov: 60; near: 0.1; far: 10000')
      // Desabilitar look-controls quando usando AR.js (o AR.js controla a câmera)
      camera.setAttribute('look-controls', 'enabled: false')
      camera.setAttribute('wasd-controls', 'enabled: false')
      camera.setAttribute('position', '0 1.6 0')
      sceneEl.appendChild(camera)
    }

    // Adicionar luzes
    let ambientLight = sceneEl.querySelector('#ambient-light')
    if (!ambientLight) {
      ambientLight = document.createElement('a-entity')
      ambientLight.id = 'ambient-light'
      ambientLight.setAttribute('light', 'type: ambient; color: #888')
      sceneEl.appendChild(ambientLight)
    }

    let directionalLight = sceneEl.querySelector('#directional-light')
    if (!directionalLight) {
      directionalLight = document.createElement('a-entity')
      directionalLight.id = 'directional-light'
      directionalLight.setAttribute('light', 'type: directional; color: #fff; intensity: 0.6')
      directionalLight.setAttribute('position', '1 1 1')
      sceneEl.appendChild(directionalLight)
    }

    // Adicionar Image Tracking com AR.js
    // Para usar: mostre o marker Hiro na câmera (pode ser impresso ou em outra tela)
    // Marker Hiro: https://jeromeetienne.github.io/AR.js/data/images/HIRO.jpg
    let imageMarker = document.getElementById('image-marker')
    if (!imageMarker) {
      // Criar marker de imagem
      imageMarker = document.createElement('a-marker')
      imageMarker.id = 'image-marker'

      // Usar o marker padrão do AR.js (Hiro)
      // Você pode imprimir ou mostrar em outra tela: https://jeromeetienne.github.io/AR.js/data/images/HIRO.jpg
      imageMarker.setAttribute('type', 'pattern')
      imageMarker.setAttribute('url', 'https://cdn.jsdelivr.net/gh/AR-js-org/AR.js@master/data/data/patt.hiro')
      imageMarker.setAttribute('smooth', 'true')
      imageMarker.setAttribute('smoothCount', '10')
      imageMarker.setAttribute('smoothTolerance', '0.01')
      imageMarker.setAttribute('smoothThreshold', '5')

      // Criar cubo que aparece quando o marker é detectado
      const trackedCube = document.createElement('a-box')
      trackedCube.setAttribute('position', '0 0.5 0')
      trackedCube.setAttribute('rotation', '0 45 45')
      trackedCube.setAttribute('color', '#4CC3D9')
      trackedCube.setAttribute('width', '1')
      trackedCube.setAttribute('height', '1')
      trackedCube.setAttribute('depth', '1')

      // Animação de rotação contínua
      trackedCube.setAttribute('animation', 'property: rotation; to: 0 405 45; loop: true; dur: 10000')

      // Adicionar animação de escala para feedback visual
      const scaleAnimation = document.createElement('a-animation')
      scaleAnimation.setAttribute('attribute', 'scale')
      scaleAnimation.setAttribute('dur', '2000')
      scaleAnimation.setAttribute('repeat', 'indefinite')
      scaleAnimation.setAttribute('from', '1 1 1')
      scaleAnimation.setAttribute('to', '1.2 1.2 1.2')
      scaleAnimation.setAttribute('direction', 'alternate')
      trackedCube.appendChild(scaleAnimation)

      imageMarker.appendChild(trackedCube)
      sceneEl.appendChild(imageMarker)

      console.log('✅ Image tracking marker adicionado - Mostre o marker Hiro na câmera')
      console.log('📄 Marker Hiro: https://jeromeetienne.github.io/AR.js/data/images/HIRO.jpg')
    }

    // Limpar objetos quando o componente desmontar
    return () => {
      const cameraEl = document.getElementById('camera')
      if (cameraEl) {
        cameraEl.remove()
      }
      const ambientLightEl = sceneEl.querySelector('#ambient-light')
      if (ambientLightEl) {
        ambientLightEl.remove()
      }
      const directionalLightEl = sceneEl.querySelector('#directional-light')
      if (directionalLightEl) {
        directionalLightEl.remove()
      }
      const imageMarkerEl = document.getElementById('image-marker')
      if (imageMarkerEl) {
        imageMarkerEl.remove()
      }
    }
  }, [usarAFrame])

  return (
    <div className="ar-game-screen">
      {/* Loading overlay */}
      {arLoading && (
        <div className="ar-loading-overlay">
          <div className="ar-loading-content">
            <div className="ar-loading-spinner"></div>
            <p className="ar-loading-text">Preparando AR...</p>
          </div>
        </div>
      )}

      {/* A-Frame Scene (se habilitado) */}
      {usarAFrame && (
        <ARSceneAFrame />
      )}

      {/* Face Tracker (se habilitado) */}
      {usarFaceTracking && (
        <FaceTracker enabled={true} />
      )}
    </div>
  )
}
